.boxVote {
  background-color: #282c34;
  color: white;
  height: 50vh;
  width: 50vw;
  margin: auto;
  padding: 1.5em;
  text-align: center;
  font-size: 30px;
  margin-bottom: 1em;
}

.divVote{
  display: flex;
  align-items: center;
  justify-content: center;
}

@media only screen and (max-width: 767px) {
    .boxVote {
        min-width: 95%;
        min-height: 100vh;
      }
  }

.btn-danger{
    font-size: 25px !important;
}