.divWeather{
    position: flex;
    margin: auto;
    padding: 15px;
    border-radius: 5px;
    min-height: 350px;
    background-color: #282c34;
    padding-top: 2em;
}

@media only screen and (max-width: 767px) {
    .boxWeather {
        width: 95%;
        margin: 0;       
    }
  }

.weather__key{
    color: #f16051;
    border-bottom: solid 2px rgba(255, 255, 255, 0.06);
    font-weight: 400;
} 

.weather__key:last-child {
    border: 0;
}

.weather__value {
    color: #fff;
    font-weight: 200;
}

.weather__error {
    color: #f16051;
    font-size: 20px;
    letter-spacing: 1px;
    font-weight: 200;
}

.div-form{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 1em;
}