h1 {
  color: #111;
  font-family: "Open Sans Condensed", sans-serif;
  font-size: 64px;
  font-weight: 700;
  line-height: 64px;
  margin: 0 0 0;
  padding: 20px 30px;
  text-align: center;
  text-transform: uppercase;
}

h2 {
  color: #111;
  font-family: "Open Sans Condensed", sans-serif;
  font-size: 48px;
  font-weight: 700;
  line-height: 48px;
  margin: 0 0 24px;
  padding: 0 30px;
  text-align: center;
  text-transform: uppercase;
}

p {
  color: #111;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  line-height: 28px;
  margin: 0 0 48px;
}

a {
  color: #990000;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

hr{
  border-color: #f16051;
}

.date {
  color: #111;
  display: block;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  position: relative;
  text-align: center;
  z-index: 1;
}

.date:before {
  border-top: 1px solid #111;
  content: "";
  position: absolute;
  top: 12px;
  left: 0;
  width: 100%;
  z-index: -1;
}

.author {
  color: #111;
  display: block;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  padding-bottom: 38px;
  position: relative;
  text-align: center;
  z-index: 1;
}

.author:before {
  border-top: 1px solid #111;
  content: "";
  position: absolute;
  top: 12px;
  left: 0;
  width: 100%;
  z-index: -1;
}

.date span,
.author span {
  background: #fdfdfd;
  padding: 0 10px;
  text-transform: uppercase;
}

.line {
  border-top: 1px solid #111;
  display: block;
  margin-top: 60px;
  padding-top: 50px;
  position: relative;
}

.read-more {
  -moz-border-radius: 50%;
  -moz-transition: all 0.2s ease-in-out;
  -webkit-border-radius: 50%;
  -webkit-transition: all 0.2s ease-in-out;
  background: #111;
  border-radius: 50%;
  border: 10px solid #fdfdfd;
  color: #fff;
  display: block;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  height: 80px;
  line-height: 80px;
  margin: -40px 0 0 -40px;
  position: absolute;
  bottom: 0px;
  left: 50%;
  text-align: center;
  text-transform: uppercase;
  width: 80px;
}

.read-more:hover {
  background: #990000;
  text-decoration: none;
}

.navbar-nav{
  margin: auto;
  align-items: center;
}

.nav-item{
  margin: 0, 0.5em;
}

input[type="text"]{
  background-color: transparent;
  border: 0;
  border-bottom: solid 1px #f16051;
  width: 100%;
  padding-bottom: 4px;
  color: #fff !important;
  font-weight: lighter;
  letter-spacing: 2px;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
  outline: none;
}

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px #2c3e50 inset;
  -webkit-text-fill-color: #fff !important;
}

.btn-orange{
  border:0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #f16051;
  color: #fff;
  font-weight: 100;
  width: 100%;
}

.btn-orange:active {
  outline: none;
}

.btn-orange:hover {
  cursor: pointer;
}
